import React, { Component } from 'react';
import { Form, Button } from "react-bootstrap";
import { connect } from 'react-redux';
import TextField from '@material-ui/core/TextField';
import * as actions from '../../../../store/actions/auth';
import './Login.css'

class Login extends Component {

    state = {
        values: {
            'email': '',
            'password': ''
        }
    }

    onChange = (field, e) => {
        let val = e.target.value;
        let curVal = this.state.values;
        curVal[field] = val;

        this.setState({
            values: curVal
        });
    }

    handleSubmit = (e) => {
        e.preventDefault();
        let value = this.state.values;
        this.props.onAuth(value.email, value.password);
    }

    render() {
        let errorMessage = null;
        if (this.props.error) {
            errorMessage = (
                <p>{this.props.error.message}</p>
            );
        }

        return (
            <div className="login-main">
                <Form className="login-form" onSubmit={this.handleSubmit}>
                    {errorMessage}
                    <div className="welcome-head">Welcome Back !</div>

                    <div className="login-margin login-email-input">
                        <TextField
                            required
                            label="Email Address"
                            variant="outlined"
                            name='email'
                            onChange={(e) => this.onChange('email', e)}
                            fullWidth
                        />
                    </div>

                    <div className="login-margin login-password-input">
                        <TextField
                            required
                            label="Password"
                            type="password"
                            variant="outlined"
                            name="password"
                            onChange={(e) => this.onChange('password', e)}
                            fullWidth
                        />
                    </div>

                    <a href='/register'>Forgot your password ?</a>

                    <div className="login-submit-btn">
                        <Button variant="warning" type="submit">
                            {this.props.loading ? 'Please Wait...' : 'Log In'}
                        </Button>
                    </div>
                </Form>
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        loading: state.loading,
        error: state.error
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onAuth: (email, password) => dispatch(actions.authLogin(email, password))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Login);