import React, { Component } from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom';
import './App.css';
import MainPage from './components/MainPage/MainPage';
import RegisterPage from './components/RegisterPage/RegisterPage';
import Root from './components/Root/Root';
import { connect } from 'react-redux';
import * as actions from './store/actions/auth';

import ResumeOne from './resume-templates/resume17/resume17';
import ResumeTwo from './resume-templates/resumeTwo/resumeTwo';
import ResumeThree from './resume-templates/resumeThree/resumeThree';
import ResumeFour from './resume-templates/resumeFour/resumeFour';
import ResumeFive from './resume-templates/resumeFive/resumeFive';
import ResumeSix from './resume-templates/resumeSix/resumeSix';
import ResumeSeven from './resume-templates/resumeSeven/resumeSeven';
import ResumeEight from './resume-templates/resumeEight/resumeEight';
import ResumeNine from './resume-templates/resumeNine/resumeNine';
import ResumeTen from './resume-templates/resumeTen/resumeTen';
import Resume11 from './resume-templates/resume11/resume11';
import Resume12 from './resume-templates/resume12/resume12';
import Resume13 from './resume-templates/resume13/resume13';
import Resume14 from './resume-templates/resume14/resume14';
import Resume15 from './resume-templates/resume15/resume15';
import Resume17 from './resume-templates/resumeOne/resumeOne';

import PrivateRoute from './routes';

class App extends Component {

  componentDidMount() {
    this.props.onTryAutoSignup();
  }

  render() {
    return (
      <Router>
        <Route path={"/"} exact render={() => <Root><MainPage /></Root>} />
        <Route path={"/register"} render={() => <Root><RegisterPage /></Root>} />
        {/* Routes for Resume Templates */}
        {/* <Route path={"/resume-edit-1"} render={() => <CustomHeader><ResumeOne /></CustomHeader>} /> */}
        <PrivateRoute path={"/resume-edit-1"} component={<ResumeOne />} />
        <PrivateRoute path={"/resume-edit-2"} component={<ResumeTwo />} />
        <PrivateRoute path={"/resume-edit-3"} component={<ResumeThree />} />
        <PrivateRoute path={"/resume-edit-4"} component={<ResumeFour />} />
        <PrivateRoute path={"/resume-edit-5"} component={<ResumeFive />} />
        <PrivateRoute path={"/resume-edit-6"} component={<ResumeSix />} />
        <PrivateRoute path={"/resume-edit-7"} component={<ResumeSeven />} />
        <PrivateRoute path={"/resume-edit-8"} component={<ResumeEight />} />
        <PrivateRoute path={"/resume-edit-9"} component={<ResumeNine />} />
        <PrivateRoute path={"/resume-edit-10"} component={<ResumeTen />} />
        <PrivateRoute path={"/resume-edit-11"} component={<Resume11 />} />
        <PrivateRoute path={"/resume-edit-12"} component={<Resume12 />} />
        <PrivateRoute path={"/resume-edit-13"} component={<Resume13 />} />
        <PrivateRoute path={"/resume-edit-14"} component={<Resume14 />} />
        <PrivateRoute path={"/resume-edit-15"} component={<Resume15 />} />
        <PrivateRoute path={"/resume-edit-17"} component={<Resume17 />} />
      </Router>
    );
  }
}


const mapStateToProps = state => {
  return {
    isAuthenticated: state.token !== null
  }
}

const mapDispatchToProps = dispatch => {
  return {
    onTryAutoSignup: () => dispatch(actions.authCheckState())
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(App);