import React, { Component } from 'react'
import Bounce from "react-reveal/Bounce";
import Flip from "react-reveal/Flip";
import { Image } from 'react-bootstrap'
import ResumeImage from '../../../assets/images/boosted-resume-banner.svg';
// import { Link } from 'react-router-dom';
import './SectionOne.css';

export default class SectionOne extends Component {
    render() {
        return (
            <div className="section-1">
                <div className="textBox-1">
                    <Bounce top>
                        <h1 style={styles.h1}>
                            Kickstart Your <span className="highlightText">Job search</span> With an Eye-catching Professional Resume.
                    </h1>
                    </Bounce>

                    <Bounce left>
                        <p style={styles.paraOne}>
                            HiredIn resume builder is specially designed for freshers.
                            Curated and approved by recruiters of fortune 500 companies.
                    </p>
                    </Bounce>

                    {/* <Bounce bottom>
                        <Button style={styles.buildButton} variant="warning">
                            <span style={styles.buttonText}>
                                <Link to='/resume' style={styles.buttonText}>
                                    Build Now
                                </Link>
                            </span>
                        </Button>
                    </Bounce> */}
                </div>

                <Flip top>
                    <div className="imageBox-1">
                        <Image style={styles.image} src={ResumeImage} />
                    </div>
                </Flip>
            </div>
        );
    };
};

const styles = {
    buildButton: {
        borderRadius: '20px',
        backgroundColor: 'black',
        border: 'none'
    },
    buttonText: {
        fontSize: '18px',
        padding: '5px',
        textDecoration: 'none',
        color: '#fee425',
    },
    paraOne: {
        fontSize: '20px',
        fontWeight: '500',
        marginBottom: '20px'
    },
    h1: {
        fontSize: '45px',
        marginBottom: '20px'
    },
    image: {
        height: '400px'
    }
}
